
.content {
    grid-area: content;
    padding: 10px;
}
.imageHeader {
    padding: 1px;
    width: 100%;
}
.LogoImage {
    width: 50px;
    border-radius: 30px;
}
.text{
    padding-top: 10px;
    padding-right: 10px;
    text-align: center;
}
.myLink {
    display: flex;
}
.mainLink {
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}

.linkHover:hover {
    color: rgb(168, 83, 83);
}