
.imageHeader {
    padding: 1px;
    width: 100%;
}
.bigLogo{
    border-radius: 20px;
}
.content {
    grid-area: content;
    padding: 10px;
    display: flex;
}
.catWork{
    width: 250px;
    border-radius: 40px;
}
.leftBlock{
    padding: 10px;
}
.rightBlock {
    padding: 10px;
}
.containerLogo .btn {
    position: relative;
    bottom: 290px;
    left: 5%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #f1f1f1;
    color: black;
    font-size: 12px;
    padding: 5px 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
}
.input{
    display: none;
}
.containerLogo .btn:hover {
    background-color: black;
    color: white;
}
.error{
    border-radius: 20px;
    padding: 2px;
    text-align: center;
    color: rgb(211, 17, 17);
    background-color: white;
}
a {
    display: inline-block;
}
.contactsLink {
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}
.linkHover:hover {
    color: rgb(168, 83, 83);
}