.friendsBar{
  padding: 10px;
}
.LogoImage {
    width: 45px;
    border-radius: 35px;
}
.navLink {
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 8px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}
.linkHover:hover {
    color: rgb(168, 83, 83);
}
.dialog h3{
    color: black;
    margin-top: 20px;
    margin-bottom: 5px;
    font-size: 20px;
    margin-top: 0px;
    letter-spacing: 0.02em;
    font-family: "Open Sans",
    sans-serif;
    font-weight: 400;
    text-align: center;
    line-height: 1.62em;
}